<template>
    <div style="position:relative" v-bind:class="{'open':openSuggestion}">
        <input class="form-control" id="licencia" name="licencia" type="text" v-validate="'required'" :value="value" @input="updateValue($event.target.value)"
          @keydown.enter = 'enter'
          @keydown.down = 'down'
          @keydown.up = 'up'
        >
        <span v-show="errors.has('licencia')" class="glyphicon glyphicon-remove form-control-feedback" aria-hidden="true"></span>
        <span v-show="errors.has('licencia')" class="help is-danger">{{ errors.first('licencia') }}</span>

        <ul class="dropdown-menu" style="width:100%">
            <li v-for="(suggestion, index) in matches"
                v-bind:class="{'active': isActive(index)}"
                @click="suggestionClick(index)"
            >
              <span>{{ suggestion.license }}</span>
            </li>
            <li @click="closeSuggestion()"><span>Quitar sugerencias</span></li>
        </ul>
    </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      required: true
    },
    suggestions: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      open: false,
      current: 0
    }
  },
  computed: {
    // Filtering the suggestion based on the input
    matches () {
      return this.suggestions.filter((obj) => {
        return obj.license.toLowerCase().indexOf(this.value.toLowerCase()) >= 0
      })
    },
    openSuggestion () {
      return this.matches.length !== 0 &&
             this.open === true
    }
  },
  methods: {
    closeSuggestion(){
      this.open = false;
      this.openSuggestion;
    },
    updateValue (value) {
      if (this.open === false) {
        this.open = true
        this.current = 0
      }
      if(value === ''){
        this.open = false;
      }
      this.$emit('input', value);
    },
    // When enter pressed on the input
    enter () {
      this.$emit('input', this.matches[this.current].license)
      this.open = false
    },
    // When up pressed while suggestions are open
    up () {
      if (this.current > 0) {
        this.current--
      }
    },
    // When up pressed while suggestions are open
    down () {
      if (this.current < this.matches.length - 1) {
        this.current++
      }
    },
    // For highlighting element
    isActive (index) {
      return index === this.current
    },
    // When one of the suggestion is clicked
    suggestionClick (index) {
      this.$emit('input', this.matches[index].license)
      this.open = false
    }
  }
}
</script>


